/** @jsx React.createElement */

import React from 'react';
import {storiesOf, addDecorator} from '@storybook/react';
import { withState } from '@dump247/storybook-state';
import { withReadme }  from 'storybook-readme';
import { withKnobs, text, selectV2 } from '@storybook/addon-knobs/react';

import TJInput from './tj-input.jsx';
import CurrencyReadme from './currency-input.md';
import TextReadme from './text-input.md';

addDecorator(withKnobs);

const INPUT_THEMES = {
    Main : 'main',
    Underlined : 'underlined'
};

storiesOf('Components/TJInput', module)
	.add('Текст',
		withReadme(TextReadme, withState(
			{
				text: 'Sample text'
			},
			(store) =>
				<TJInput
                    theme={selectV2('Тема', INPUT_THEMES, 'main')}
					type='text'
					value={store.state.text}
					placeholder={text('Placeholder')}
					onInput={(value) =>  {
						store.set({text:  value})
					}}
				/>
			)
		)
	)
	.add('Денежный',
		withReadme(CurrencyReadme, withState(
			{
				salary: 100000
			},
			(store) =>
				<TJInput
                    theme={selectV2('Тема', INPUT_THEMES, 'main')}
					type='currency'
					value={store.state.salary}
                    after={text('Текст после числа', '₽')}
					length={9}
					onInput={(value) => {
						store.set({salary:  value})
					}}
				/>
			)
		)
	);


