/** @jsx React.createElement */
import React from 'react';
import {storiesOf, addDecorator} from '@storybook/react';
import { withState } from '@dump247/storybook-state';
import { withReadme }  from 'storybook-readme';
import { withKnobs, text, boolean, number } from '@storybook/addon-knobs/react';

import TJMonthpicker from './tj-monthpicker.jsx';
import Readme from './tj-monthpicker.md';

addDecorator(withKnobs);

storiesOf('Components', module)
	.add('TJMonthpicker',
		withReadme(Readme, withState(
			{
				year: 2016,
				month: 9,
			},
			(store) =>
				<TJMonthpicker
					year={store.state.year}
					month={store.state.month}
					minYear={number('Минимальный год', 2016)}
					minMonth={number('Минимальный месяц', 2)}
					maxYear={number('Максимальный год', 2025)}
					maxMonth={number('Максимальный месяц', 6)}
					alwaysCustom={boolean('Всегда отображать кастомный пикер')}
					onDateChange={(month, year) =>  {
						store.set({year, month})
					}}
				/>
			)
		)
	);


