/** @jsx React.createElement */

import React from 'react';

import { configure, addDecorator } from '@storybook/react';
import { setOptions } from '@storybook/addon-options';
import backgroundColor from 'react-storybook-decorator-background';


require('../assets/styles/amp/reset.css');
require('../assets/styles/base/props.css');
require('../assets/styles/base/mixins.css');
require('../assets/styles/base/fonts.css');

import downloadFonts from '../assets/scripts/common/fontfaceobserver';

const tjcStyles = require.context('../assets/styles/editorial/tj-common-components/', true, /\.css$/);

const componentsStories = require.context('../assets/scripts/special/tjc-react', true, /\.stories\.js$/);

const extraStories = require.context('../assets/scripts/special/sb-stories/', true, /\.stories\.js$/);


setOptions({
	name: 'Т—Ж',
	addonPanelInRight: true,
});



addDecorator((storyFn) => {
    return (
        <div className='tj-article--storybook'>
            { storyFn() }
        </div>
    )
});

addDecorator(backgroundColor(['#1f97c9', '#ffffff']));

function loadStories() {
	componentsStories.keys().forEach((filename) => componentsStories(filename));
	extraStories.keys().forEach((filename) => extraStories(filename));
	tjcStyles.keys().forEach((filename) => tjcStyles(filename));

	downloadFonts();
}

configure(loadStories, module);

