/** @jsx React.createElement */

import React from 'react';

const SECTION_HEADERS = {
    main : 'tj-section-header',
    card : 'tj-card__header',
};

export function TJSectionHeader({ theme, children }) {

    const
        className = SECTION_HEADERS[theme] || SECTION_HEADERS.main;

    return (
        <h2 className={className}>
            { children }
        </h2>
    )
}


const SECTION_TEXTS = {
    main : {
        className : 'tj-section-text'
    },
    card :  {
        className : 'tj-card__text'
    }
};

export function TJSectionText({theme = 'main', children}) {

    const {
        className,
    } = SECTION_TEXTS[theme];

    return (
        <p className={className}>
            {children}
        </p>
    )
}
