/** @jsx React.createElement */

import React from 'react';
import {formatNumber, getCaretPosition, setCaretPosition, isValidDate, reverseString } from "../../../common/utils";
import {BEMBlock} from "../utils";


const FORMATTER_INTERFACE = [
	'toRaw',
	'toFormatted',
	'isCorrect',
	'normalize',
	'placeholder',
	'delimiter',
	'after',
	'keyboard',
];

class AbstractFormatter {

	constructor() {
		this.placeholder = '';
		this.delimiter = '';
		this.after = '';
		this.keyboard = 'text';
	}

	toRaw(v) {
		return v;
	}

	toFormatted(v) {
		return v;
	}

	shouldUpdate(value, length) {
		return !length || (value.toString().length <= length);
	}

	isCorrect() {
		return true;
	}

	normalize(v) {
		return v
	}
}

class NumberFormatter extends AbstractFormatter {

	constructor() {
		super();
		this.placeholder = '0';
		this.keyboard = 'tel';

	}

	toRaw(inputString) {
		if (inputString === '') {
			return 0;
		}
		return Number(inputString)
	}

	toFormatted(rawValue) {
		if (rawValue !== 0) {
			return rawValue.toString()
		}
		return ''
	}

	shouldUpdate(value, length) {
		return super.shouldUpdate(value, length) && !isNaN(value)
	}

}

class FloatFormatter extends AbstractFormatter {

	constructor() {
		super();
		this.placeholder = '0';
		this.keyboard = 'url';
	}

	toRaw(inputString) {
		if (inputString === '') {
			return ''
		}

		let [first, ...temp] = inputString.replace(/[^\d,]/, '').split(',');

		return first + (temp.length ? ',' + temp.join('') : '');
	}


	shouldUpdate(value, length) {
		return super.shouldUpdate(value, length) && value.split(',').length < 3
	}


	toFormatted(rawValue) {
		return rawValue;
	}

	normalize(rawValue) {
		return Number(rawValue.replace(',', '.'))
	}

}

class CurrencyFormatter extends NumberFormatter {

	constructor() {
		super();
		this.placeholder = '0 ₽';
		this.delimiter = ' ';
		this.after = '₽';
	}

	toRaw(inputString) {
		if (inputString === '') {
			return 0;
		}
		return Number(inputString.replace(/\s*/g, ''))
	}

	toFormatted(rawValue) {
		if (rawValue !== 0) {
			return formatNumber(rawValue)
		}
		return ''
	}

}

class DateFormatter extends NumberFormatter {

	constructor() {
		super();
		this.placeholder = 'дд/мм/гггг';
		this.delimiter = '/';
	}

	toRaw(inputString) {
		return inputString.replace(/\//g, '')
	}

	toFormatted(rawValue) {
		let valueString = rawValue.toString(),
			i = 0,
			result = '';

		while (i < valueString.length) {
			if (i === 2 || i === 4) {
				result += '/';
			}
			result += valueString[i];
			i++;
		}
		return result
	}

	shouldUpdate(value, length) {
		return super.shouldUpdate(value, length) && value.toString().length <= 8
	}

	isCorrect(inputValue) {
		return inputValue.length === 10 && isValidDate(inputValue)
	}
}

class CustomFormatter extends AbstractFormatter {

	constructor(options, base) {
		super();
		this.shouldUpdateFromBase = () => { return true; };
		this.shouldUpdateFromOptions = () => { return true; };
		if (base && base instanceof AbstractFormatter) {
			FORMATTER_INTERFACE.forEach( prop => {
				this[prop] = base[prop];
			});
			this.shouldUpdateFromBase = base.shouldUpdate;
		}
		if (options) {
			FORMATTER_INTERFACE.forEach( prop => {
				if (options[prop]) {
					this[prop] = options[prop];
				}
			});
			if (options.shouldUpdate) {
				this.shouldUpdateFromOptions = options.shouldUpdate
			}
		}
		this.shouldUpdate = (value, length) => {
			return this.shouldUpdateFromOptions(value, length) && this.shouldUpdateFromBase(value, length)
		}
	}

}

const FORMATTERS = {};

FORMATTERS.text = new AbstractFormatter();
FORMATTERS.currency = new CurrencyFormatter();
FORMATTERS.float = new FloatFormatter();
FORMATTERS.number = new NumberFormatter();
FORMATTERS.date = new DateFormatter();
FORMATTERS.percentage = new CustomFormatter({
	placeholder : '0 %',
	after : '%',
}, FORMATTERS.number);

const INPUT_THEMES = {
    main : {
        bem : new BEMBlock('tj-react-input'),
    },
    underlined : {
        bem : new BEMBlock('tj-underlined-input'),
    },
};

export default class TJInput extends React.Component {
	constructor(props) {
		super(props);

		this._onKeyDown = this._onKeyDown.bind(this);
		this._onClick = this._onClick.bind(this);
		this._onInput = this._onInput.bind(this);
		this._onChange = this._onChange.bind(this);
		this._onBlur = this._onBlur.bind(this);
		this._onFocus = this._onFocus.bind(this);
		this._onSelect = this._onSelect.bind(this);

		this._shouldUpdateCaretPosition = false;

		if (props.formatter) {
			this.formatter = new CustomFormatter(props.formatter, FORMATTERS[this.props.type || 'text']);
		} else {
			this.formatter = FORMATTERS[this.props.type || 'text'];
		}

		this.state = {
			caretPosition : 0,
			inputValue : this.formatter.toFormatted(props.value),
			isFocused: false,
			selected: false
		};
	}

	_onKeyDown(e) {
		const {
			inputValue,
			caretPosition
		} = this.state;

		switch (e.key) {
			case 'ArrowLeft' :
				this.setState( {
					caretPosition : Math.max(0, caretPosition - 1)
				});
				this._shouldUpdateCaretPosition = false;
				break;
			case 'ArrowRight' :
				this.setState( {
					caretPosition : Math.min(inputValue.length, caretPosition + 1)
				});
				this._shouldUpdateCaretPosition = false;
				break;
			default:
				this._shouldUpdateCaretPosition = true;
				break;
		}

		this.props.onKeyDown && this.props.onKeyDown(e);
	}

	_onInput(e) {
		const {
			inputValue,
			caretPosition,
			selected
		} = this.state;

		const {
			inputType,
			target
		} = e;

		const {
			toRaw,
			toFormatted,
			delimiter,
			shouldUpdate,
			isCorrect,
			normalize,
		} = this.formatter;

		const {
			length,
			min,
			max,
		} = this.props;

		let newValue = target.value;

		let caretDiff = 0;


		if (inputType === 'deleteContentBackward' && !selected) {
			caretDiff --;

			if (inputValue[caretPosition - 1] === delimiter) {
				newValue = inputValue.substring(0, caretPosition - 2) + inputValue.substring(caretPosition);
			}
		}

		let rawValue = toRaw(newValue);

		if(delimiter &&
			inputValue.substring(0, caretPosition).split(delimiter).length >
			toFormatted(rawValue).substring(0, caretPosition - 1).split(delimiter).length) {
			caretDiff--;
		}

		if (shouldUpdate(rawValue, length)) {
			if (min && rawValue < min) {
				rawValue = min;
			}
			if (max && rawValue > max) {
				rawValue = max;
			}
			newValue = toFormatted(rawValue);

			if (inputType !== 'deleteContentBackward' && selected) {
				caretDiff = 1;
			} else if (newValue.length > inputValue.length) {
				caretDiff = newValue.length - inputValue.length;
			}

			let newCaretPosition = Math.max(0, caretPosition + caretDiff);

			this.setState({
				caretPosition: newCaretPosition,
				inputValue: newValue,
				selected : false,
			});

			this.props.onInput && this.props.onInput(toRaw(newValue), isCorrect(newValue), normalize(toRaw(newValue)));
		} else {
			this.forceUpdate();
		}


	}

	_onChange(e) {
		this.props.onChange && this.props.onChange(e);
	}

	_onBlur(e) {
		this.props.onBlur && this.props.onBlur(e);

		this._componentFocused = false;
		this.setState({isFocused: false});
	}

	_onFocus(e) {
		this.props.onFocus && this.props.onFocus(e);

		this._componentFocused = true;
		this.setState({isFocused: true});
	}

	_onClick(e) {

		let {
			start,
			end
		} = getCaretPosition(e.target);

		this.setState({
			caretPosition: start,
			selected : start !== end
		});

		this._componentFocused = true;

		this.props.onClick && this.props.onClick(e);
	}

	_onSelect(e) {

		this.setState({
			caretPosition: e.target.selectionStart,
			selected : e.target.selectionStart !== e.target.selectionEnd
		});

		this._componentFocused = true;

		this.props.onSelect && this.props.onSelect(e);
	}



	render() {

		const {
			props,
			state
		} = this;

		const
			inputValue = state.inputValue;

		const {
			isCorrect,
			placeholder,
			keyboard,
		} = this.formatter;

		const BEM = INPUT_THEMES[props.theme].bem;

		const after = props.after || this.formatter.after;

		return <div className={BEM.mod('focused', state.isFocused).exec()}>
			<input
				ref={(input) => {
					this._inputRef = input
				}}
				className={BEM.el('field').mod('correct', isCorrect(inputValue), 'wrong').exec()}
				type={keyboard}
				onKeyDown={this._onKeyDown}
				onInput={this._onInput}
				onClick={this._onClick}
				onKeyUp={props.onKeyUp}
				onChange={this._onChange}
				onFocus={this._onFocus}
				onBlur={this._onBlur}
				onSelect={this._onSelect}
				value={inputValue}
				placeholder={props.placeholder || placeholder}
			/>
			{ (inputValue && after)
				? <label className={BEM.el('label').exec()}>
					{inputValue}
					<span className={BEM.el('after').exec()}>&nbsp;{after}</span>
				</label>
				: ''
			}
		</div>
	}


	componentWillReceiveProps(nextProps) {
		this.setState({ inputValue : this.formatter.toFormatted(nextProps.value) });
	}

	componentDidUpdate() {
		if (this._componentFocused && this._shouldUpdateCaretPosition) {
			setCaretPosition(this._inputRef, this.state.caretPosition, this.state.caretPosition);
			this._shouldUpdateCaretPosition = false;
		}
	}
}
