/** @jsx React.createElement */
import React from 'react';
import {storiesOf, addDecorator} from '@storybook/react';
import { withState } from '@dump247/storybook-state';
import { withReadme }  from 'storybook-readme';
import { withKnobs, text, boolean, number } from '@storybook/addon-knobs/react';
import Readme from './tj-select.md';
import {TJSelectDefault, TJSelectFiltered, TJSelectMenu} from "./tj-select";
import {TJSectionHeader} from "../tj-article-elements/tj-text-elements";

addDecorator(withKnobs);

storiesOf('Components/TJSelect', module)
	.add('Дефолтный',
		withReadme(Readme,
			withState( {
				options : [
					{ value : '#f00', name : 'Красный'},
					{ value : '#0f0', name : 'Зелёный'},
					{ value : '#00f', name : 'Синий'},
					{ value : '#fff', name : 'Белый'},
					{ value : '#000', name : 'Чёрный'},
				],
				selected : {
					value : '#f00',
					name : 'Красный',
				}
			},
			(store) =>
				<div>
					<TJSelectDefault
						selected={store.state.selected}
						options={store.state.options}
                        onSelect={(option) =>  {
							store.set({
								selected : option
							})
						}}
					/>
					<div style={{backgroundColor : store.state.selected.value, width : '100px', height : '100px'}} />
				</div>
			)
		)
	)
    .add('С фильтрацией',
        withReadme(Readme,
            withState( {
                    options : [
                        { value : '#f00', name : 'Красный'},
                        { value : '#0f0', name : 'Зелёный'},
                        { value : '#00f', name : 'Синий'},
                        { value : '#fff', name : 'Белый'},
                        { value : '#000', name : 'Чёрный'},
                    ],
                    selected : {
                        value : '#f00',
                        name : 'Красный',
                    }
                },
                (store) =>
                    <div>
                        <TJSelectFiltered
                            selected={store.state.selected}
                            options={store.state.options}
                            onSelect={(option) =>  {
                                store.set({
                                    selected : option
                                })
                            }}
                        />
                        <div style={{backgroundColor : store.state.selected.value, width : '100px', height : '100px'}} />
                    </div>
            )
        )
    )
    .add('Меню',
        withReadme(Readme,
            withState( {
                    options : [
                        { value : '#f00', name : 'Красный'},
                        { value : '#0f0', name : 'Зелёный'},
                        { value : '#00f', name : 'Синий'},
                        { value : '#fff', name : 'Белый'},
                        { value : '#000', name : 'Чёрный'},
                        { value : '#ff0', name : 'Жёлтый'},
                        { value : '#f0f', name : 'Розовый'},
                        { value : '#0ff', name : 'Альтернативный'},
                        { value : '#ffcd33', name : 'Пожалуй лучший'},
                    ],
                    selected : {
                        value : '#f00',
                        name : 'Красный',
                    }
                },
                (store) =>
                    <div>
                        <TJSelectMenu
                            selected={store.state.selected}
                            options={store.state.options}
                            heading={<TJSectionHeader>Цвет</TJSectionHeader>}
                            maxMenuHeight={150}
                            onSelect={(option) =>  {
                                store.set({
                                    selected : option
                                })
                            }}
                        />
                        <div style={{backgroundColor : store.state.selected.value, width : '100px', height : '100px'}} />
                    </div>
            )
        )
    );

