// TODO: Добавить конфиг
export class BEMBlock{

	constructor(name, { elDelim = '__', modDelim = '--', modValueDelim = '--' } = {}) {
		this._block = name;
		this._value = name;
		this._element = name;

		this._elDelim = elDelim;
        this._modDelim = modDelim;
        this._modValueDelim = modValueDelim;
	}

	exec() {
		let result = this._value;
		this._value = this._block;
		this._element = this._block;
		return result
	}

	el(elName) {
		this._element = this._block + this._elDelim + elName;
		this._value = this._element;
		return this;
	}

	mod(mod, value = true, altMod) {
		if (typeof value === 'string') {
			this._value += ' ' + this._element + this._modDelim + mod + this._modValueDelim + value;
		} else if (typeof value === 'boolean') {
            mod = mod ? this._modDelim + mod : '';
            altMod = altMod ? this._modDelim + altMod : '';
			this._value += ' ' + this._element + (value ? mod : altMod);
		}
		return this;
	}
}
