/** @jsx React.createElement */

import React from 'react';
import {BEMBlock} from "../utils";
import TJInput from '../tj-input/tj-input.jsx';
import {TJStrong} from "../tj-article-elements/tj-text-style";

export class TJSelectDefault extends React.Component {

	state = {
        expanded : false,
    };

    componentDidMount() {
        window.addEventListener('click', this.clickOutsideOfSelect);
    }


    clickOutsideOfSelect = (event) => {
        if (!this.selectContainerRef.contains(event.target)) {
            this.setState({ expanded: false });
        }
    };

    setSelectContainerRef = (element) => {
        this.selectContainerRef = element
    };

	onSelect = (option) => () => {
        this.setState({ expanded : false });
        this.props.onSelect(option);
    };

	onToggle = () => {
		this.setState({ expanded : !this.state.expanded });
	};

	render() {

        return (
            <TJSelectExpandableContainer
                theme='main'
                containerRef={this.setSelectContainerRef}
                expanded={this.state.expanded}
                controller={
                    <TJSelectControllerButton
                        theme='main'
                        onToggle={this.onToggle}
                        text={this.props.selected.name}
                        expanded={this.state.expanded}
                    />
                }
            >
                <TJSelectOptionList
                    theme='main'
                    expanded={this.state.expanded}
                >
                    { this.props.options.map(option => {
                        return <TJSelectOption
                            key={option.name}
                            theme='main'
                            selected={this.props.selected.name === option.name}
                            onSelect={this.onSelect(option)}
                        >
                            {option.name}
                        </TJSelectOption>
                    })}
                </TJSelectOptionList>
            </TJSelectExpandableContainer>
        )
	}

    componentWillUnmount() {
        window.removeEventListener('click', this.clickOutsideOfSelect)
    }

}


export class TJSelectFiltered extends React.Component {

    state = {
        expanded : false,
        filter : ''
    };

    componentDidMount() {
        window.addEventListener('click', this.clickOutsideOfSelect);
    }

    clickOutsideOfSelect = (event) => {
        if (!this.selectContainerRef.contains(event.target)) {
            this.setState({ expanded: false });
        }
    };

    setSelectContainerRef = (element) => {
        this.selectContainerRef = element
    };

    onSelect = (option) => () => {
        this.setState({
            expanded : false,
            filter : '',
        });

        this.props.onSelect(option);
    };

    onInputFocus = () => {
        this.setState({ expanded : true });
    };

    onFilterChange = (newFilter) => {
        this.setState({ filter : newFilter });
    };

    render() {

        const {
            expanded,
            filter
        } = this.state;

        const {
            selected,
            options,
            filterFunction
        } = this.props;

        return (
            <TJSelectExpandableContainer
                theme='filtered'
                containerRef={this.setSelectContainerRef}
                expanded={expanded}
                controller={
                    <TJInput
                        theme='main'
                        type='text'
                        value={filter}
                        placeholder={selected.name}
                        onFocus={this.onInputFocus}
                        onInput={this.onFilterChange}
                    />
                }
            >
                <TJSelectOptionList
                    theme='main'
                    expanded={expanded}
                >
                    { options
                        .filter(option => {
                            return filterFunction
                                ? filterFunction(option, filter)
                                : option.name.indexOf(filter) !== -1
                        })
                        .map(option => {
                            return <TJSelectOption
                                key={option.name}
                                theme='main'
                                selected={selected.name === option.name}
                                onSelect={this.onSelect(option)}
                            >
                                {option.name}
                            </TJSelectOption>
                        })
                    }
                </TJSelectOptionList>
            </TJSelectExpandableContainer>
        )
    }

    componentWillUnmount() {
        window.removeEventListener('click', this.clickOutsideOfSelect)
    }

}


export class TJSelectMenu extends React.Component {

    onSelect = (option) => () => {
        this.props.onSelect(option);
    };

    render() {

        const {
            selected,
            options,
        } = this.props;

        return (
            <TJSelectMenuContainer
                theme='main'
                heading={this.props.heading}
            >
                <TJSelectOptionListWrapper
                    maxHeight={this.props.maxMenuHeight}
                >
                    <TJSelectOptionList
                        theme='menu'
                    >
                        { options
                            .map(option => {
                                return <TJSelectOption
                                    key={option.name}
                                    theme='main'
                                    selected={selected.name === option.name}
                                    onSelect={this.onSelect(option)}
                                >
                                    {option.name}
                                </TJSelectOption>
                            })
                        }
                    </TJSelectOptionList>
                </TJSelectOptionListWrapper>
            </TJSelectMenuContainer>
        )
    }

}

const
    SELECT_EXPANDABLE_CONTAINER_THEMES = {
        main : 'tj-select-default',
        filtered : 'tj-select-filtered',
    };

export function TJSelectExpandableContainer({ controller, theme, containerRef, children, expanded }) {

    const
        className = SELECT_EXPANDABLE_CONTAINER_THEMES[theme] || SELECT_EXPANDABLE_CONTAINER_THEMES.main;

    return (
        <div
            ref={containerRef}
            className={className}
        >
            { controller }
            { expanded
                ? children
                : ''
            }
        </div>
    )
}

const
    SELECT_MENU_CONTAINER_THEMES = {
        main : 'tj-select-menu',
    };

export function TJSelectMenuContainer({ heading, theme, children }) {

    const
        className = SELECT_MENU_CONTAINER_THEMES[theme] || SELECT_MENU_CONTAINER_THEMES.main;

    return (
        <div
            className={className}
        >
            <div className={className + '__heading'}>
                { heading }
            </div>
            { children }
        </div>
    )
}

const
    SELECT_BUTTON_CONTROLLER_THEMES = {
        main : 'tj-select-expand-button',
    };

export function TJSelectControllerButton({ theme,  text, onToggle }) {

    const
        className = SELECT_BUTTON_CONTROLLER_THEMES[theme] || SELECT_BUTTON_CONTROLLER_THEMES.main;

    return (
        <button
            className={className}
            onClick={onToggle}
        >
            { text }
        </button>
    )
}

const
    SELECT_OPTION_LIST_WRAPPER_THEMES = {
        main : 'tj-select-option-list-wrapper',
    };

export function TJSelectOptionListWrapper({ theme, children, maxHeight }) {

    const
        className = SELECT_OPTION_LIST_WRAPPER_THEMES[theme]
            || SELECT_OPTION_LIST_WRAPPER_THEMES.main;

    return (
        <div
            className={className}
            style={{maxHeight : maxHeight ? maxHeight + 'px' : 'none'}}
        >
            { children }
        </div>
    )
}

const
    SELECT_OPTION_LIST_THEMES = {
        main : 'tj-select-option-list',
    };

export function TJSelectOptionList({ theme, children }) {

    const
        className = SELECT_OPTION_LIST_THEMES[theme] || SELECT_OPTION_LIST_THEMES.main;

	return (
		<div
            className={className}
		>
            { children }
		</div>
	)
}

const
    SELECT_OPTION_THEMES = {
        main : 'tj-select-option',
    };


export function TJSelectOption({ selected, onSelect, theme, children}) {

    const
        className = SELECT_OPTION_THEMES[theme] || SELECT_OPTION_THEMES.main;

	return (
		<button
			className={className + (selected ? ` ${className}--selected` : '')}
			onClick={onSelect}
		>
			{ children }
		</button>
	)
}


