import FontFaceObserver from 'fontfaceobserver';

const FONTS = [
	{
		name: 'Graphik',
		variations: [{weight: 300}, {weight: 400}, {weight: 500}, {weight: 600}],
		testClass: 'grotesque-font-loaded',
		cookieKey: 'tj.grotesquefont'
	},
	{
		name: 'Charter',
		variations: [{weight: 400}],
		testClass: 'serif-font-loaded',
		cookieKey: 'tj.seriffont'
	}
];
const FONT_LOAD_WAIT_TIMEOUT = 10 * 1000; // 10 seconds
const FONT_COOKIE_LIFETIME = 30 * 24 * 60 * 60 * 1000; // 1 month

export default () => {
	FONTS.filter(font => !document.body.classList.contains(font.testClass)).forEach(font => {
		let observers = font.variations.map(variation => {
			return new FontFaceObserver(font.name, variation)
				.load(null, FONT_LOAD_WAIT_TIMEOUT)
				.catch(error => console.warn('FontFaceObserver error', error));
		});
		Promise.all(observers).then(() => {
			let date = new Date();
			date.setTime(date.getTime() + FONT_COOKIE_LIFETIME);
			document.documentElement.classList.add(font.testClass);
			document.cookie = `${font.cookieKey}=1;path=/;expires=${date.toGMTString()};`;
		})
		.catch(error => console.warn(`${font.name} load error`, error));
	});
}
