export function replaceHistoryState(url, title) {
	history.replaceState({
		title: title,
		url: url
	}, title, url);
	document.title = title;
}

export function formatNumber(number, decimals = 0) {
	let
		result,
		parts = String(number.toFixed(decimals)).split('.'),
		integer = parts[0],
		fraction = parts[1],
		slice, slices = [];

	if (integer > 9999) {
		while (integer.slice(-3) !== '') {
			slice = integer.slice(-3);
			slices.push(slice);
			integer = integer.substr(0, integer.length - 3);
		}
		//result = slices.reverse().join(' ');
		result = slices.reverse().join(' ');
	}
	else {
		result = integer;
	}
	if (fraction) {
		result += ',' + fraction;
	}
	return result;
}

export function clearNumber(str) {
	return str.replace(/[^\d.]/g, '');
}

export function hexToRgb(hex) {
	hex = hex.replace('#', '');
	let r = parseInt(hex.substring(0, 2), 16);
	let g = parseInt(hex.substring(2, 4), 16);
	let b = parseInt(hex.substring(4, 6), 16);
	return [r, g, b];
}

export function loadYoutubeAPI() {
	if (document.querySelector('#youtube_player_api')) {
		return null;
	}
	let script = document.createElement('script');
	script.src = 'https://www.youtube.com/player_api';
	script.id = 'youtube_player_api';
	document.head.appendChild(script);
	return script;
}

export function getDocumentScrollTop() {
	return window.pageYOffset || document.documentElement.scrollTop;
}

export function getDocumentHeight() {
	return Math.max(document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight);
}

export function debounce(func, wait, immediate) {
	let timeout;
	return function () {
		let context = this, args = arguments;
		let later = () => {
			timeout = null;
			if (!immediate) {
				func.apply(context, args);
			}
		};
		let callNow = immediate && !timeout;
		clearTimeout(timeout);
		timeout = setTimeout(later, wait);
		if (callNow) {
			func.apply(context, args);
		}
	}
}

// @See https://medium.com/@_jh3y/throttling-and-debouncing-in-javascript-b01cad5c8edf
export function throttle(func, limit) {
	let inThrottle;
	let lastFunc;
	let lastRan;
	return function () {
		const context = this;
		const args = arguments;
		if (!inThrottle) {
			func.apply(context, args);
			lastRan = Date.now();
			inThrottle = true;
		} else {
			clearTimeout(lastFunc);
			lastFunc = setTimeout(function () {
				if ((Date.now() - lastRan) >= limit) {
					func.apply(context, args);
					lastRan = Date.now();
				}
			}, limit - (Date.now() - lastRan))
		}
	}
}


export function isEmpty(obj) {
	return (Object.getOwnPropertyNames(obj).length === 0);
}

export function offset(el) {
	if ('getBoundingClientRect' in el) {
		let boundingRect = el.getBoundingClientRect(),
			body = document.body || document.getElementsByTagName("body")[0],
			clientTop = document.documentElement.clientTop || body.clientTop || 0,
			clientLeft = document.documentElement.clientLeft || body.clientLeft || 0,
			scrollTop = (window.pageYOffset || document.documentElement.scrollTop || body.scrollTop),
			scrollLeft = (window.pageXOffset || document.documentElement.scrollLeft || body.scrollLeft);
		return {
			top: boundingRect.top + scrollTop - clientTop,
			left: boundingRect.left + scrollLeft - clientLeft
		}
	}
}

export function distanceBetweenElements(id1, id2) {
	return Math.abs(document.getElementById(id1).offsetTop - document.getElementById(id2).offsetTop);
}

export function setFeatureDataIntoLocalStorage(name, value) {
	let featureData = JSON.parse(localStorage.getItem('features'));
	if (!featureData) {
		featureData = {}
	}
	featureData[name] = value;
	localStorage.setItem('features', JSON.stringify(featureData));
}

export function getFeatureDataFromLocalStorage(name) {
	let featureData = JSON.parse(localStorage.getItem('features'));
	return (featureData ? (featureData[name] ? featureData[name] : '') : '')
}

const MONTH_NAMES = [
		'Январ',
		'Феврал',
		'Март',
		'Апрел',
		'Ма',
		'Июн',
		'Июл',
		'Август',
		'Сентябр',
		'Октябр',
		'Ноябр',
		'Декабр'
	],
	ENDINGS = [
		{ // Большинство месяцев
			'n': 'ь',
			'a': 'ь',
			'g': 'я',
			'd': 'ю',
			'i': 'ем',
			'p': 'е',
		},
		{ // Март и Август
			'n': '',
			'a': '',
			'g': 'а',
			'd': 'у',
			'i': 'ом',
			'p': 'е',
		},
		{ // Май
			'n': 'й',
			'a': 'й',
			'g': 'я',
			'd': 'ю',
			'i': 'ем',
			'p': 'е',
		}
	];

/* Памятка падежей для метода getMonthName
*	Nominative (n) - Именительный
*	Accusative (a) - Винительный
*	Genitive (g) - Родительный
* 	Dative (d) - Дательный
*   Instrumental (i) - Творительный
*   Prepositional (p) - Предложный
 */

export function getMonthName(monthNumber, grammaticalCase = 'n', form = 'full') {
	let type = 0;
	if (monthNumber === 2 || monthNumber === 7) { // Март и Август
		type = 1;
	} else if (monthNumber === 4) { // Май
		type = 2;
	}

	let resultString = MONTH_NAMES[monthNumber] + ENDINGS[type][grammaticalCase];

	if (form === 'full') {
		return resultString;
	} else if (form === 'short') {
		return resultString.substring(0, 3)
	}
}

export function monthName(monthNumber) {
	switch (Number(monthNumber)) {
		case 1 :
			return 'Январь';
		case 2 :
			return 'Февраль';
		case 3 :
			return 'Март';
		case 4 :
			return 'Апрель';
		case 5 :
			return 'Май';
		case 6 :
			return 'Июнь';
		case 7 :
			return 'Июль';
		case 8 :
			return 'Август';
		case 9 :
			return 'Сентябрь';
		case 10 :
			return 'Октябрь';
		case 11 :
			return 'Ноябрь';
		case 12 :
			return 'Декабрь';
		default :
			return '! Неправильный номер месяца !'
	}
}

