/** @jsx React.createElement */

import React from 'react';

const TABLE_THEMES = {
    main : {
        className : 'tj-table',
        bodyClassName : 'tj-table__body',
        headClassName : 'tj-table__head'
    },
};

export function TJTable({theme = 'main', headRow, children}) {
    const {
        className,
        bodyClassName,
        headClassName,
    } = TABLE_THEMES[theme];

    return (
        <table className={className}>
            <thead className={headClassName}>{headRow}</thead>
            <tbody className={bodyClassName}>{children}</tbody>
        </table>
    )
}


const TABLE_ROW_THEMES = {
    main : {
        className : 'tj-table__row'
    },
    head : {
        className : 'tj-table__head-row'
    },
};

export function TJTableRow({theme = 'main', children}) {
    const {
        className
    } = TABLE_ROW_THEMES[theme];

    return (
        <tr className={className}>
            {children}
        </tr>
    )
}

const TABLE_HEAD_THEMES = {
    main : {
        className : 'tj-table__head-cell'
    }
};

export function TJTableHead({theme = 'main', width, children}) {
    const {
        className
    } = TABLE_HEAD_THEMES[theme];

    return (
        <th className={className} width={width}>
            {children}
        </th>
    )
}

const TABLE_CELL_THEMES = {
    main : {
        className : 'tj-table__cell'
    }
};

export function TJTableCell({theme = 'main', children}) {
    const {
        className
    } = TABLE_CELL_THEMES[theme];

    return (
        <td className={className}>
            {children}
        </td>
    )
}
