/** @jsx React.createElement */

import React from 'react';

const CARDS = {
    main : {
        className : 'tj-card'
    }
};

export function TJCard ({ theme = 'main', children, header }) {

    const {
        className,
    } = CARDS[theme];

    return (
        <section className={className}>
            { header }
            { children }
        </section>
    )
}



const CARD_SECTIONS = {
    main : {
        className : 'tj-card__section'
    }
};

export function TJCardSection ({ theme = 'main', children }) {

    const {
        className,
    } = CARD_SECTIONS[theme];

    return (
        <section className={className}>
            { children }
        </section>
    )
}
